<template>
	<view class="container">
		<image class="app-logo-img" :src="`${Api.featureImg}/user/login-bg.jpg`" mode="widthFix"></image>
		<!-- ËÆæÁΩÆÁôΩËâ≤ËÉåÊôØÈò≤Ê≠¢ËΩØÈîÆÁõòÊää‰∏ãÈÉ®ÁªùÂØπÂÆö‰ΩçÂÖÉÁ¥†È°∂‰∏äÊù•Áõñ‰ΩèËæìÂÖ•Ê°ÜÁ≠â -->
		<view class="wrapper">
			<button class="btn-login" open-type="getUserInfo" @getuserinfo="wxGetUserInfo" withCredentials="true">ÁôªÂΩï</button>
		</view>
		<view class='get-pop'>
			<view class='pop-box'>
				<view style='margin-bottom:20px;line-height:1.8em;padding-left:26rpx'>
					<view>ÊÇ®ÁöÑÊâãÊú∫Âè∑Â∞öÊú™Ê≥®ÂÜåÔºå</view>
					<view>ËØ∑ÊéàÊùÉ"Ëé∑ÂèñÊâãÊú∫Âè∑"Ôºå</view>
					<view>‰ª•ÂÆåÊàêÂø´ÈÄüÊ≥®ÂÜå„ÄÇ</view>
				</view>
				<button type="primary" open-type="getPhoneNumber" @getphonenumber="getPhoneNumber" >Ëé∑ÂèñÊâãÊú∫Âè∑</button>
			</view>
		</view>
	</view>
