<script>
import { mapMutations, mapState } from 'vuex';
import Api from '@/common/api';
export default {
	components: {
		
	},
	data() {
		return {
			Api,
			loginInfo: {},
			phone: '13146587722',
			isWeiXin: 0,
			showGetPhone: false,
			info: '',
			userinfoIv: '',
			userinfoEncryptedData: ''
		};
	},
	onShareAppMessage: function(res) {},
	onLoad() {
		let that = this
		let isWeiXin = that.$common.isWeiXinBrowser(); //Âà§Êñ≠ÊòØÂê¶Âú®ÂæÆ‰ø°ÊµèËßàÂô® trueÊòØ
		if (isWeiXin) {
			that.isWeiXin = 1;
		}
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		...mapMutations(['login', 'logout']),
		// ÂæÆ‰ø°Ëé∑ÂèñÊâãÊú∫Âè∑
		getPhoneNumber(e) {
            console.log("üöÄ ~ file: index.vue ~ line 55 ~ getPhoneNumber ~ e", e) 
			tt.login({
				success: ({code, anonymousCode}) => {
					console.log('Ëé∑Âèñcode', code)
					if (code) {
						if (e.detail.iv) {
							let params = {
								anonymous_Code: anonymousCode,
								code,
								iv: e.detail.iv,
								encryptedData: e.detail.encryptedData
							}
							this.loginBy(params)
						}
					}
				}
			})
		},
		// Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
		wxGetUserInfo: function(e) {
			this.$db.set('wxLoginInfo', e.detail.userInfo)
			uni.login({
				provider: 'weixin',
				success: ({code}) => {
					if (code) {
						wx.getUserInfo({
							success: res => {
								const { rawData, iv, signature, encryptedData } = res
								if (!rawData || !iv || !signature || !encryptedData) {
									return this.outError('Ëé∑ÂèñÁôªÂΩï‰ø°ÊÅØÈîôËØØ[wx]')
								}
								let params = {
									code,
									userInfo: rawData,
									userinfoEncryptedData: encryptedData,
									userinfoIv: iv,
								}
								this.checkLogin(params)
							}
						})
					} else {
						wx.showToast({
							title: 'Áî®Êà∑ÁôªÂΩïÂá≠ËØÅËé∑ÂèñÂ§±Ë¥•ÔºåËØ∑Êà™ÂõæËÅîÁ≥ªÂÆ¢Êúç',
							icon: 'none'
						})
					}
				}, fail() {
					wx.showToast({
						title: 'Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•ÔºåËØ∑Êà™ÂõæËÅîÁ≥ªÂÆ¢Êúç',
						icon: 'none'
					})
				}
			})
		},
		// Ê£ÄÊü•ÊòØÂê¶Ê≥®ÂÜå
		async checkLogin(params) {
			let res = await Api.apiSelfCall('post',Api.BASEURI + Api.index.checkLogin, params, true);
			params.canLogin = res.canLogin
			if (res.canLogin === 1) {
				uni.login({
					provider: 'weixin',
					success: ({code}) => {
						this.loginByWX({...params, code})
					}
				})
			} else {
				// Â¶ÇÊûúÊ≤°ÊúâÊ≥®ÂÜåÔºåÈúÄË¶ÅÂÖàËé∑ÂèñÊâãÊú∫Âè∑ÔºåÂÜçËµ∞Ê≥®ÂÜåÊµÅÁ®ã
				this.loginInfo = params
				this.showGetPhone = true
			}
		},
		// ÁôªÂΩï
		loginBy(params) {
			console.log('111',params)
			// return
			let that = this;
			uni.showLoading({
				title: 'ËØ∑Á®çÂÄô',
				mask: true
			});
			uni.request({
				url: Api.BASEURI + Api.auth.tikTokLogin,
				method: 'post',
				data: params,
				success: ({data}) => {
					uni.hideLoading();
					if (data.code == 500 && data.data == 'JSONObject["openid"] not found.') {
						this.showToastData('appidÂá∫Èîô')
					} else if (data.code === 200) {
						this.showGetPhone = false
							that.login(data.data);
							that.$db.set('token', data.data.tokenHead + data.data.token);
							that.getuserinfo()
					} else {
						this.showToastData('Ëé∑ÂèñunionIdÂ§±Ë¥•ÔºåËØ∑Êà™ÂõæËÅîÁ≥ªÂÆ¢Êúç')
					}
				},
				fail: function(e) {
					console.log('Ëé∑ÂèñunionIdÂ§±Ë¥•ÔºåËØ∑Êà™ÂõæËÅîÁ≥ªÂÆ¢Êúç', e);
					uni.hideLoading();
					this.showToastData('Ëé∑ÂèñunionIdÂ§±Ë¥•ÔºåËØ∑Êà™ÂõæËÅîÁ≥ªÂÆ¢Êúç')
				}
			});
		},
		showToastData(msg, type = 'none') {
			uni.showToast({
				title: msg,
				icon: type
			});
		},
		outError(e) {
			if (typeof e === 'string') {
			wx.showModal({
				title: 'ËØ∑ÊääÊ≠§Êä•ÈîôÊà™ÂõæÁªôÂÆ¢Êúç',
				content: e,
				success(res) {
				if (res.confirm) {
					console.log('Áî®Êà∑ÁÇπÂáªÁ°ÆÂÆö')
				} else if (res.cancel) {
					console.log('Áî®Êà∑ÁÇπÂáªÂèñÊ∂à')
				}
				}
			})
			} else {
			try {
				wx.showModal({
				title: 'ËØ∑ÊääÊ≠§Êä•ÈîôÊà™ÂõæÁªôÂÆ¢Êúç',
				content: JSON.stringify(e),
				success(res) {
					if (res.confirm) {
					console.log('Áî®Êà∑ÁÇπÂáªÁ°ÆÂÆö')
					} else if (res.cancel) {
					console.log('Áî®Êà∑ÁÇπÂáªÂèñÊ∂à')
					}
				}
				})
			} catch (error) {
				wx.showModal({
				title: 'ËØ∑ÊääÊ≠§Êä•ÈîôÊà™ÂõæÁªôÂÆ¢Êúç',
				content: 'ÂæÆ‰ø°ÂºÄÂèëËÉΩÂäõË∞ÉÁî®ÂºÇÂ∏∏' + e,
				success(res) {
					if (res.confirm) {
					console.log('Áî®Êà∑ÁÇπÂáªÁ°ÆÂÆö')
					} else if (res.cancel) {
					console.log('Áî®Êà∑ÁÇπÂáªÂèñÊ∂à')
					}
				}
				})
			}
			}
		},
		// Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
		async getuserinfo() {
			this.showToastData('ÁôªÂΩïÊàêÂäü')
			let data = await Api.apiCall('get', Api.index.userInfo, {}, true);
			if (data) {
				if (data.member) {
					uni.setStorageSync('userInfo', data.member);
					uni.navigateBack();
				} else {
					this.logout();
				}
			}
		}
	}
};
